/*reset样式导入*/
import './lib/scss/reset.scss'
/*Vue组件引入*/
import Vue from 'vue'
import Logo from './components/header'
import Hello from './components/Hello'
import Signed from './components/signed'
import Signin from './components/unsigned/Signin'
import Signup from './components/unsigned/Signup'
import User from './components/signed/user'
import Game from './components/signed/game'
import vueResource from 'vue-resource'
import VueRouter from 'vue-router'
import App from './components/App'

import promise from './lib/js/myPromise.js'




Vue.use(vueResource);
Vue.use(VueRouter);

//路由管理
var router = new VueRouter({hashbang:false});
router.map({  
  '/':{
    component: Hello,
    subRoutes:{
      '/signin':{
        component:Signin,
      },
      '/signup':{
        component:Signup, 
      } ,
    }
  },
  '/signed/:userName':{
    name: 'signed',
    component: Signed,
    subRoutes:{
      '/':{
        component: User
      },
      '/user':{
        name: 'user',
        component: User
      },
      '/game':{
        name: 'game',
        component: Game
      }
    }
  },
});
router.start(App,'#app');
