export default{
	do(str){
		let result = [];
		for(let i=0;i<9;i++){
			let j;
			let arr = [];
			if(i!==8){
				j=str.slice(i*9,i*9+9);	
			}else{
				j=str.slice(i*9);
			}
			for(let k=0;k<j.length;k++){
				let p;
				let bool;
				if(k!==8){
					p = j.slice(k,k+1);
				}else{
					p = j.slice(k);
				}

				bool = p=='%'? true:false;
				p = p=='%'? '':parseInt(p);

				arr.push({num: p, bool: bool});
			}
			result.push(arr);
		}
		
		
		return result;
	}
}


