import varMap from './varMap.js'
export default{
	do(data){
		let level=parseInt(data.current.mission.slice(0,1));
		let minS = data.gameInfo['level'+level].min;
		let avgS = data.gameInfo['level'+level].avg;

		let cap = varMap.arg(minS,level);
		let real = varMap.arg(avgS,level);
		return {
			cap: cap.a + 600 * cap.k,
			real: real.a + 600 * real.k,
		}
	}
}


