<template>
    <router-view :Data="Data"></router-view>
</template>
<script>
	
	import Hello from './Hello'
	import Signed from './signed'

	export default{
		data(){
			return {
				 
    			Data: null,
			}
		},
		events: {
		    'signed':function(data){      
		      this.Data = data;
		      this.$route.router.replace({name: 'signed',params:{userName:data.userName}});
		    }
	  	},
		
	  	components: { Hello , Signed}
	}
</script>
<style></style>


