<template>
	<router-view :Sinfo="selfInfo" :Rinfo="rankInfo"></router-view>
	<modal :show.sync="error" effect="fade" width="400">
	  <div slot="modal-header" class="modal-header">
	    <h4 class="modal-title">
	      <code>服务器或数据库错误</code>
	    </h4>
	  </div>
	  <div slot="modal-body" class="modal-body">
	  	请联系作者: QQ: 387857274 或 gloria_n@yeah.net
	  </div>
	  <div slot="modal-footer" class="modal-footer">
	    <button type="button" class="btn btn-success" @click='error = false'>关闭</button>
	  </div>
	</modal>
</template>
<script>
	//外部模块
	import Format from '../lib/js/observeAndFormat.js'
	import CapAndReal from '../lib/js/CapAndReal.js'
	import toBoardArray from '../lib/js/toBoardArray.js'
	import cookie from 'cookie_js'
	//Vue组件
	import User from './signed/user'
	import Game from './signed/game'
	import VueStrap from 'vue-strap'
	

	function FromatData(data){
		
		//开始游戏时需要的数据
		data.level = data.current.mission.slice(0,2);
		data.levelNum = data.current.mission.slice(0,1);
		data.num = data.current.mission.slice(2);
		//计算并赋值cap,real
		Object.assign(data,CapAndReal.do(data));
		//将所有数值转换为文字
		data = Format.do(data);
		//添加detail,current,boardInfo字段
		data.detail = {
			First:data.gameInfo.level1,
			Sec:data.gameInfo.level2,
			Thir:data.gameInfo.level3,
			Forth: data.gameInfo.level4,
		};
		
		data.boardInfo = data.current;
		data.current = data.current.mission;

		return data
	}
	export default{
		data(){
			return {
				selfInfo:null,
				rankInfo: {
					level: this.Data.rankInfo.level,
					cap: this.Data.rankInfo.cap,
					real: this.Data.rankInfo.real
				},
				error: false,
			}
		},
		events: {
			'start': function(){
				//发出查询棋盘请求
				let userName = this.selfInfo.userName;
				let level = this.selfInfo.levelNum;
				let subLevel = this.selfInfo.num;

				this.$http.get('/user/findInfo?userName='+userName+'&level='+level+'&subLevel='+subLevel).then((res)=>{
					let data = res.json();
					//解析棋盘

					this.selfInfo.Board = toBoardArray.do(data.board);
					this.selfInfo.boardInfo = data.boardInfo;
					//跳转游戏界面
					this.$route.router.replace({name:'game',append: true});

					
				}).catch((err)=>{
					//触发错误提示
					this.error = true;
					this.$broadcast('error');
				})
				
			},
			'backToUser': function(){
				this.$route.router.replace('/signed/'+this.$route.params.userName+'/user');	
			}
		},
		ready(){

			let UserCookie = cookie.get('user');
			
			this.Data = UserCookie? JSON.parse(UserCookie.slice(2)):this.Data;  
			
			this.selfInfo = FromatData(this.Data);


		},
		components: {
			User,Game,
			snipper: VueStrap.spinner,
			modal: VueStrap.modal,
		},
		props: ['Data'],
	}
</script>


