<template>
  <table>
  	<tr class="th"><th>名次</th><th>玩家姓名</th><th>等级</th></tr>
    <tr v-for="(index,player) in list"><td>{{index+1}}.</td><td> {{player.userName}} </td><td>{{player.level}}</td></tr>
  </table>
</template>

<script>
export default{
  props:['list']
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	$width-small:  400px;
	$width-medium: 760px;
	$width-large:  1200px;
	@mixin responsive($width) {
	    @if $width == wide-screens {
	        @media only screen and (max-width: $width-large) { @content; }
	    }
	    @else if $width == medium-screens {
	        @media only screen and (max-width: $width-medium) { @content; }
	    }
	    @else if $width == small-screens {
	        @media only screen and (max-width: $width-small) { @content; }
	    }
	}
 	table {
		width: 100%;
		margin: 0 auto;
		th{
			padding-top: 5px;
			padding-bottom: 5px;
			text-align: center;
		}
		td {
	 		width:70px;
	 		height:30px;
	 		text-align: center;
 		}
 		.th {
 			border-bottom: 1px solid #ddd;

 		}
 		@include responsive(wide-screens) {
 			margin-top: -15px;
		}
	}
</style>



