<template>
	<div id="User">
			<!-- <user-info :Sinfo="Sinfo"></user-info
			><detail-info :Sinfo="Sinfo"></detail-info
			><rank-info :Rinfo="Rinfo"></rannk-info>
			 -->
		<div class="wraper">
			<div class="self-info">
		      	<div class="content">
			        <img src="../../assets/avatar.jpg"><br>
			        <span class="name">{{ Sinfo.userName }}</span><br>
			        <span class="level">等级:{{ Sinfo.level }}</span><br>
			        <span class="cap">能力值:{{ Sinfo.cap }}</span><br>
			        <span class="real">水平值:{{ Sinfo.real }}</span><br>
			        
			        <div class="btnset">
			        	<button class="btn btn-danger detailbtn" @click="detail=true">个人信息</button>
			            <button class="btn btn-info rankbtn" @click="rank=true">排行榜</button>
			            <button class="btn btn-success" @click="notify()">开始挑战</button>
			        </div>
		        </div>
	      	</div>
		  	<div class="detail-info">
		    	<h1>详细统计</h1>
		    	<ul class="d-table">
		    		<li class="d-cell">
		    			<h3>修罗</h3>
		    			<ul class="d-content">
		    				<li><span class="lable min">最短用时</span>: {{Sinfo.detail.First.min}}</li>
		    				<li><span class="lable avg">平均用时</span>: {{Sinfo.detail.First.avg}}</li>
		    			</ul>
		    		</li
		    		><li class="d-cell">
		    			<h3>罗汉</h3>
		    			<ul class="d-content">
		    				<li><span class="lable min">最短用时</span>: {{ Sinfo.detail.Sec.min }}</li>
		    				<li><span class="lable avg">平均用时</span>: {{ Sinfo.detail.Sec.avg }}</li>
		    			</ul>
		    		</li
		    		><li class="d-cell">
		    			<h3>菩萨</h3>
		    			<ul class="d-content">
		    				<li><span class="lable min">最短用时</span>: {{ Sinfo.detail.Thir.min }}</li>
		    				<li><span class="lable avg">平均用时</span>: {{ Sinfo.detail.Thir.avg }}</li>
		    			</ul>
		    		</li
		    		><li class="d-cell">
		    			<h3>佛祖</h3>
		    			<ul class="d-content">
		    				<li><span class="lable min">最短用时</span>: {{ Sinfo.detail.Forth.min }}</li>
		    				<li><span class="lable avg">平均用时</span>: {{ Sinfo.detail.Forth.avg }}</li>
		    			</ul>
		    		</li>
		    	</ul>
		    	<button class="now-stage btn btn-warning">当前所处关卡：{{ Sinfo.current }}</button>
		    </div> 	
		</div>
		

	    <div id="rank-info" class="rank-info">
	        <div class="rank-area">
	            <h1>排行榜
	                <span class="options">
	                    <a @click="level()">等级</a>
	                    <a @click="cap()">能力值</a>
	                    <a @click="real()">水平值</a>
	                </span>
	            </h1>
	            
	            <list :is="currentView" :list="players"></list>   
	        </div>
    	</div>
    	
	</div>
	<modal title="排行榜" :show.sync="rank" ok-text="赞一个" cancel-text="关闭" effect="zoom" width="400">
			<div slot="modal-header" class="modal-header rank-modal-header"></div>
			<div slot="modal-body" class="modal-body" id="rank-modal-body">
			
		        <div class="rank-area">
		            
		            <tabs>
		            	<tab header="等级">
		            		<list :is="'level'" :list="players"></list>		
		            	</tab>
		            	<tab header="能力值">
		            		<list :is="'cap'" :list="players"></list>		
		            	</tab>
		            	<tab header="水平值">
		            		<list :is="'cap'" :list="players"></list>		
		            	</tab>
		            </tabs>		
		               
		        </div>
			</div>
			<div slot="modal-footer" class="modal-footer rank-modal-footer">
				<button type="button" class="btn btn-default" @click='rank=false'>关闭</button>
			</div>
	</modal>
	<modal :show.sync="detail" effect="fade" width="400">
	  <div slot="modal-header" class="modal-header">
	    <h4 class="modal-title">
	      <b>个人信息</b>
	    </h4>
	  </div>
	  <div slot="modal-body" class="modal-body detail-modal-body">
	    	<ul class="d-table">
	    		<li class="d-cell l1">
	    			<h3>修罗</h3>
	    			<ul class="d-content">
	    				<li><span class="lable min">最短用时</span>: {{Sinfo.detail.First.min}}</li>
	    				<li><span class="lable avg">平均用时</span>: {{Sinfo.detail.First.avg}}</li>
	    			</ul>
	    		</li
	    		><li class="d-cell l2">
	    			<h3>罗汉</h3>
	    			<ul class="d-content">
	    				<li><span class="lable min">最短用时</span>: {{ Sinfo.detail.Sec.min }}</li>
	    				<li><span class="lable avg">平均用时</span>: {{ Sinfo.detail.Sec.avg }}</li>
	    			</ul>
	    		</li
	    		><li class="d-cell l3">
	    			<h3>菩萨</h3>
	    			<ul class="d-content">
	    				<li><span class="lable min">最短用时</span>: {{ Sinfo.detail.Thir.min }}</li>
	    				<li><span class="lable avg">平均用时</span>: {{ Sinfo.detail.Thir.avg }}</li>
	    			</ul>
	    		</li
	    		><li class="d-cell l4">
	    			<h3>佛祖</h3>
	    			<ul class="d-content">
	    				<li><span class="lable min">最短用时</span>: {{ Sinfo.detail.Forth.min }}</li>
	    				<li><span class="lable avg">平均用时</span>: {{ Sinfo.detail.Forth.avg }}</li>
	    			</ul>
	    		</li>
	    	</ul>
	    	<button class="now-stage btn btn-warning">当前所处关卡：{{ Sinfo.current }}</button>
	  </div>
	  <div slot="modal-footer" class="modal-footer">
	    <button type="button" class="btn btn-default" @click='detail = false'>关闭</button>
	    <button type="button" class="btn btn-success" @click='notify()'>开始挑战</button>
	  </div>
	</modal>
	<spinner v-ref:spinner :size="md" :fixed="true" text="服务器正在为您加载棋盘，请稍候......"></spinner>
	
    
</template>
<script>
	import lineCenter from '../../lib/js/lineCenter'
	import level from './user/rankInfo/level'
	import cap from './user/rankInfo/cap'
	import real from './user/rankInfo/real'
	import VueStrap from 'vue-strap'

	export default {		
		ready(){
			
		},
		
		//components: {userInfo,detailInfo,rankInfo},
		props:['Sinfo','Rinfo'],
		data(){
	        return {
	            currentView:'level',
	            rankPlayers:this.Rinfo.level,
	            capPlayers: this.Rinfo.cap,
	            realPlayers: this.Rinfo.real,
	            showRight: false,
	            showLeft: false,
	            rank: false,
	            detail: false,
	        }
    	},
	    computed: {
	        players(){
	            switch(this.currentView){
	                case 'level': return this.rankPlayers;
	                case 'cap': return this.capPlayers;
	                case 'real': return this.realPlayers;
	            }

	        },
	        
	    },
	    methods: {
	        level(){
	            this.currentView = 'level';
	        },
	        cap(){
	            this.currentView = 'cap';
	        },
	        real(){
	            this.currentView = 'real';
	        },
	        notify(){
	        	this.detail = false;
	        	this.$refs.spinner.show();
	        	setTimeout(()=>{this.$dispatch('start');},2000);
	        	
	      	}
	    },
	    events:{
	    	'error': function(){
	    		this.$refs.spinner.hide();
	    	}
	    },
	    components: {
	        level,cap,real,
	        modal: VueStrap.modal,
	        tabs: VueStrap.tabset,
			tabGroup: VueStrap.tabGroup,
			tab: VueStrap.tab,
			spinner: VueStrap.spinner,
	    },
	}
</script>
<style lang="scss" scoped>
$width-small:  400px;
$width-medium: 760px;
$width-large:  1200px;
@mixin responsive($width) {
    @if $width == wide-screens {
        @media only screen and (max-width: $width-large) { @content; }
    }
    @else if $width == medium-screens {
        @media only screen and (max-width: $width-medium) { @content; }
    }
    @else if $width == small-screens {
        @media only screen and (max-width: $width-small) { @content; }
    }
}
@mixin absCenter {
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
}
#User {
	width: 100%;
	max-width: 1200px;
	min-width: 320px;
	height: 500px;
	position: absolute;
	color: rgb(51, 51, 51);
	@include absCenter;
	@include responsive(wide-screens){
		width: 900px;
	}
	@include responsive(medium-screens){
		max-width: 500px;
		width: 100%;
		min-width: 300px;
		padding: 0 10px;
	}
	

}
.wraper {
	float: left;
	height: 100%;
	background-color: rgba(255,255,255,0.8);
	@include responsive(medium-screens){
		float: none;
	}
}


//个人信息区域

.self-info {
    position: relative;
    float: left;
    width: 300px;
    height: 100%;
    color: rgb(51, 51, 51);
    padding-top: 50px;
    padding-left: 50px;
    text-align: left;
    border-top: 1px solid #ddd;
    border-left: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    margin-right: 0px;
    @include responsive(medium-screens){
    	padding: 0;
    	width: 100%;
    	text-align: center;
    }
    .rankbtn {
      display: none;
      @include responsive(wide-screens){
        display: block;
      }

    }
    .detailbtn {
    	display: none;
    	@include responsive(medium-screens){
    		display: block;
    	}
    }
    span {
      font-size: 14px;
      
    }
    .content {
      display: inline-block;
      line-height: 1.5;
      font-size: 2.5rem;
      vertical-align: top;
      text-align: left;
      @include responsive(medium-screens){
      	margin-top: 10px;
      	text-align: center;
      }
      .name {
        font-size: 26px;
    
      }
      img {
        width: 150px;
        height: 150px;
        border: 4px solid white;
        margin-bottom: 12px;

      }
      .btnset {
        position: absolute;
        bottom: 45px;
        .btn {
              margin-top: 10px;
              width: 150px;
              font-size: 15px;
              bottom: 45px;
        }
        @include responsive(medium-screens){
        	bottom: 10px;
        }
      }
      
    }
    
  }




//详细记录区域 

.detail-info {
    float: left;
    width: 600px;
    height: 100%;
    border-top: 1px solid #ddd;
    border-right: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    @include responsive(medium-screens){
    	display: none;
    }
    h1 {
    	color: rgb(51, 51, 51);
    	margin-top: 50px;
    	font-size: 26px;
    	text-align: left;
    	border-bottom: 1px solid #ddd;
    	padding-bottom: 0.6rem;
        margin-bottom: 30px;
        text-shadow:0.5px 0 0.2px rgba(0,0,0,0.5);
    }
    .d-table {
    	.d-cell{
    		width: 50%;
    		height: 150px;
    		padding-left: 20px;
    		padding-right: 20px;
    		display: inline-block;
    		vertical-align: top;
    		h3 {
    			font-size: 20px;
    			line-height:1;
    			text-align: left;
    			border-left: 10px solid #4078c0;
                padding-left: 5px;
                margin-bottom:15px;
                text-shadow:0.5px 0 0.2px rgba(0,0,0,0.5);
    		}
    		.d-content {
    			padding-left: 20px;
    			text-align: left;
    			li {
    				padding: 5px;
                    .lable {
                        font-size: 15px;
                        letter-spacing: 2px;
                    }
                    .min {
                        color: green;
                    }
                    .avg {
                        color: #337ab7;
                    }
    			}
    		}
    	}
    }
    .now-stage {
    	width: calc(100% - 40px);
        font-size: 15px;
    }
  }
.detail-modal-body {
	
	h3 {
		font-size: 19px;
		color: white;
		padding: 5px 10px;
		margin-bottom: 10px;
	}
	li:nth-child(1) h3 {
		
		background-color: #337ab7;
		
	}
	li:nth-child(2) h3 {
		
		background-color: #5cb85c;
		
	}
	li:nth-child(3) h3 {
		
		background-color: #f0ad4e;
		
	}
	li:nth-child(4) h3 {
		
		background-color: #d9534f;
		
	}
	.d-cell {
		margin-bottom: 20px;
	}
	.d-content {
		padding-left: 10px;
	}
	.now-stage {
		display: block;
		margin: 0 auto;
	}
}



.showInc {
    
}

// 排行榜区域 

.rank-info {
    float: left;
    margin-left: 20px;
    width: 280px;
    height: 100%;
    padding-top: 50px;
    padding-left: 20px;
    padding-right: 20px;
    background-color: rgba(255,255,255,0.8);
    border: 1px solid #ddd; 
    border-radius: 3px;

    @include responsive(wide-screens){
        display: none;
    }
    .rank-area {
        width: 100%;
        padding-bottom: 10px;
        text-align: left;
        h1 {
            color: rgb(51, 51, 51);
            font-size: 26px;
            text-align: left;
            border-bottom: 1px solid #ddd;
            padding-bottom: 0.6rem;
            text-shadow:0.5px 0 0.2px rgba(0,0,0,0.5);
            a {
                font-size: 4px;
                cursor: pointer;
                text-decoration:none;
            }
            .options {
                display: inline-block;
                float: right;
            }
        }
        h3 {
            border-bottom: 2px solid white;
            padding-bottom: 0.6rem;
        }
        
    }
}
.rank-modal-header {
	padding-top: 0;
	border: none;

}


</style>


