<template>
	<div id="game">
		<div class="game-top">
			
			<div class="current">当前关卡：<span>{{ Sinfo.current }}</span></div>
				
				
			
			<div class="game-btn-set">
				<button class="btn btn-default save" @click="notify()">保存并返回</button>
			</div>
		</div
		><div class="game-right">
			<table class="board">
				<tr v-for="(index1,item1) in Board" track-by="$index">
					<td v-for="(index2,item2) in item1" :class="{red:Color[index1*9+index2].red,green:Color[index1*9+index2].green,noOrigin:Color[index1*9+index2].noOrigin}" track-by="$index" @mouseover="mouseover(index1,index2)" @mouseout="mouseout(index1,index2)" @click="click(index1,index2)">{{item2}}</td>
				</tr>
			</table>
			<div class="error">当前错误:{{error}}</div>
		</div>
		<div class="game-bottom">
			<div class="clock">已消耗时间：<span>{{ Sinfo.boardInfo.time }}</span></div>
		</div>
		<alert type="success" placement="top" :show.sync="successAlert" :duration="3000">
		  <strong>恭喜完成!</strong>
		  3秒后将自动跳转^_^~~~~
		</alert>
	</div>
</template>
<script>
import Vue from 'vue'
import board from '../../lib/js/board.js'
import timeFormat from '../../lib/js/timeFormat.js'
import toSecond from '../../lib/js/toSecond.js'
import VueStrap from 'vue-strap'

//输入状态
let inputState = {
  bool: false,
  row:-1,
  col:-1,
};
//定时器
let timer;
//已输入棋盘数据
let alreadyExist = {
	board:{},
	complete:false,
};
//生成棋盘数组
function generateArray(char,position={row:-1,col:-1}){
  let arr = [];
  for(let i=0;i<9;i++){
  let rowArray = [];
    for(let j=0;j<9;j++){
      let c ='';
      if(i===position.row&&j===position.col) c=char;
      rowArray.push(c);
    }
    arr.push(rowArray);
  }
  return arr;  
}

//棋盘数组转换为Array[81]
function Array_81(arr){
  let result = [...arr];

  for(let i=0;i<9;i++){
    result[i] = result[i].join('%');
  }
  result = result.join('%');

  return result;
}
///keyup事件控制器
function Keyup(e){
	//是否为输入状态
  
  let {row,col} = inputState;
  let board = this.Sinfo.Board;


  let bool = isOrigin.call(this,row,col);

  if((!inputState.bool) || bool) return;
  
  let key = e.keyCode===46? '' : e.keyCode-48;

  //验证是否为基本错误
  let validate = validateError.call(this,key,{row:row,col:col});

  if(validate){
  	this.Board[row].splice(col,1,key);
  	//处理记录
  	if(key==''){
  		//删除键则删除记录
  		delete alreadyExist.board[row+''+col];
  	}else{
  		//添加记录
  		alreadyExist.board[row+''+col] = key;
	}
  	
  	//检测是否完成游戏
  	var win = validateWin.call(this);
  	if(win){

  		alreadyExist.complete = true;
  		this.successAlert = true;
  		setTimeout(()=>{
			let time = toSecond.do(this.Sinfo.boardInfo.time);
			let userName = this.Sinfo.userName;
			let {board,complete} = alreadyExist;
	  		
	  		//发送更新请求
	  		this.$http.post('/user/update',{userName:userName,time:time,complete:complete,board:board}).then((res)=>{
				console.log('更新记录成功'); 
			}).catch((err)=>{
				console.log('更新记录失败');
			});

			this.$dispatch('backToUser');	
  		},3000)
  		//如果胜利
  	} 
  	
  }else{
  	//提示错误
  	this.Board[row].splice(col,1,'错误');
  	setTimeout(()=>{
		this.Board[row].splice(col,1,'');
  	},1000);	
  	this.error++;
  	return
  }
  
  
  
}
//检测游戏是否完成
function validateWin(){
	let num = 0;
	for(let i=0;i<9;i++){
		for(let j=0;j<9;j++){
			let val = this.Board[i][j];

			if(val!='') num++; 
		}
	}
	if(num===81) return true;
	return false;
}
//基本错误验证
function validateError(key,position){
	if(key=='') return true;
	let {row,col} = position;
	let Board = this.Board;

	//行循环
	for(let i=0;i<9;i++){
		let val = Board[row][i];
		if(val===key&&i!==col) return false;
	}
	//列循环
	for(let i=0;i<9;i++){
		let val = Board[i][col];
		if(val===key&&i!==row) return false;
	}
	//单个九宫格循环
	let rowS = parseInt(row/3)*3;
	let colS = parseInt(col/3)*3;

	for(let i=rowS;i<rowS+3;i++){
		for(let j=colS;j<colS+3;j++){
			let val = Board[i][j];

			if(val===key&&i!==row&&j!==col) return false;
		}
	}
	return true;
}
//初始化棋盘
function initBoard(a,boardInfo){
	let result = [];
	//赋值原始棋盘
	for(let i=0;i<9;i++){
		let row = [];
		for(let j=0;j<9;j++){
			let num = a[i][j].num;

			row.push(num);
		}
		result.push(row);
	}
	//复制已填位置
	Object.assign(alreadyExist.board,boardInfo);
	//还原用户已填位置
	for(let str in boardInfo){
		let row = parseInt(str.slice(0,1));
		let col = parseInt(str.slice(1));
		let val = parseInt(boardInfo[str]);
		result[row][col] = val;

	}
	return result;

}
//初始化表格颜色
function initColor(){
	let result = [];

	for(let i=0;i<9;i++){
		for(let j=0;j<9;j++){
			let bool = this.Sinfo.Board[i][j].bool;

			result.push({red:false,green:false,noOrigin:bool});
		}
	}
	return result;
}
//判断是否为原生棋格
function isOrigin(row,col){
	return !this.Sinfo.Board[row][col].bool
}
export default {
	data(){
		return {
			error:0,
			Board:[],
			Color:[],
			successAlert: false,
		}
	},
	computed: {
		aLoc(){
			let result = [];
			let board = this.Sinfo.boardInfo.board;

			for(let str in board){
				let row = parseInt(str.slice(0,1));
				let col = parseInt(str.slice(1));
				let num = parseInt(border[str]);


				result.push({row: row,col: col,num: num});
			}
			return  result;
		}
	},
	methods: {
		notify(){
			
			this.$dispatch('backToUser');
			
			
		},
		click(row,col){
			

	      	//判断是否为原生棋格
			if(isOrigin.call(this,row,col)) return;

			//开启输入状态，记录位置
     	 	inputState.bool = true;
	      	inputState.row = row;
	      	inputState.col = col;

			let index = row*9+col;

			this.Color[index].green = false;
			this.Color[index].red = true;
	      	
	      	
			
			
		},
		mouseout(row,col){
				//判断是否为原生棋格
				if(isOrigin.call(this,row,col)) return;

				let index = row*9+col;

				this.Color[index].green = false;
				this.Color[index].red = false;

			      //关闭输入状态，还原位置
			      inputState.bool = false;
			      inputState.row = -1;
			      inputState.col = -1;
		},
		mouseover(row,col){
			//判断是否为原生棋格
			if(isOrigin.call(this,row,col)) return;

			let index = row*9+col;

			this.Color[index].green = true;
			this.Color[index].red = false;
		},
		updateBoard(){
			//发送更新请求
			//游戏信息记录
			let time = toSecond.do(this.Sinfo.boardInfo.time);
			let userName = this.Sinfo.userName;
			let {board,complete} = alreadyExist;

			//清除定时器
			clearInterval(timer);
			//发送更新请求
			this.$http.post('/user/update',{userName:userName,time:time,complete:complete,board:board}).then((res)=>{
				//更新cookies
				console.log('更新记录成功'); 
			}).catch((err)=>{
				console.log('更新记录失败');
			});

		}
	},
	ready(){ 
		
		//初始化棋盘
		this.Board = initBoard.call(this,this.Sinfo.Board,this.Sinfo.boardInfo.board);
		//初始化表格颜色
		this.Color = initColor.call(this);
		// 转换所剩时间

		let leftTime = this.Sinfo.boardInfo.time;

		this.Sinfo.boardInfo.time = timeFormat.do(leftTime);
		// 设置定时器
		timer = setInterval(() => {
			this.Sinfo.boardInfo.time = timeFormat.do(++leftTime);
		},1000)

		// 添加输入事件
		window.onkeyup = Keyup.bind(this);
		//添加浏览器关闭,刷新事件

		
	},
	beforeDestroy(){
		//发送更新请求
		this.updateBoard();
	},
	props: ['Sinfo','Rinfo'],
	components:{
		alert: VueStrap.alert,
	}
}
</script>
<style lang="scss" scoped>
	$width-small:  540px;
	$width-medium: 760px;
	$width-large:  1200px;
	$inputHeight: 40px;
	@mixin responsive($width) {
	    @if $width == wide-screens {
	        @media only screen and (max-width: $width-large) { @content; }
	    }
	    @else if $width == medium-screens {
	        @media only screen and (max-width: $width-medium) { @content; }
	    }
	    @else if $width == small-screens {
	        @media only screen and (max-width: $width-small) { @content; }
	    }
	}
	

	#game {
		height: 100%;
		text-align: center;
	}
	.game-top {
		width: 100%;
		min-width: 320px;
		height: 100px;
		background-color: rgba(255,255,255,1);
		padding: 10px;
		line-height: 80px;
		.current,
		.clock {
			float: left;
			margin-right: 50px;
			font-size: 18px;
			
		}
		.game-btn-set {
			float: right;
			@mixin height {
				padding-top: 10px;
				padding-bottom: 10px;
			}
			.save {
				width: 100px;
				@include height;
			}
			
		}

	}
	.game-right {
		position: absolute;
		max-width: 600px;
		width: 100%;
		min-width: 320px;
		text-align: center;
		margin: 10px auto;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		@mixin border {
			border-width: 3px;
			border-color: #ddd;
			border-collapse: collapse;
			border-style: solid;
		}
		.error {
			padding: 10px 0;
			font-size: 18px;
			color: white;
		}
		table.board {
			display: inline-table;
			width: 450px;
			height: 450px;
			margin-top: 50px;
			font-weight: 600;
			background-color: white;
			border: 4px solid black;
			@include responsive(small-screens){
				width: 320px;
				height: 320px;
			}
			td {
				width: 50px;
				height: 50px;
				color:black;
				
				@include border;
				@include responsive(small-screens){
					width: 35px;
					height: 35px;
				}
				
			}
			.noOrigin {
				cursor: pointer;
				transition: all 0.2s ease-in-out;
			}
			.noOrigin.green {
				transform: scale(1.2);
				background-color: green;
			}
			.noOrigin.red {
				background-color: red;
			}
			

		}
	}
	.game-bottom {
		height: 100px;
		width: 100%;
		min-width: 320px;
		position: fixed;
		bottom: 0;
		background-color: white;
		line-height: 100px;
		font-size: 18px;
	}
</style>


