class myPromise {
	constructor(fn){
		//必须为函数类型
		if(typeof fn !== 'function') throw new Error('not a function');
		
		this.success = [];
		this.sucArg = null;

		this.fail = [];
		this.failArg = null;
		
		this.state = 0;

		this.parent = null;
		//fork promise分支
		this.fork = [];
		//传递的error
		setTimeout(()=>{
			fn(myPromise.resolve.bind(this),myPromise.reject.bind(this))	
		},0);
	}
	static resolve(value){
		if(this.state!==0) return;
		//参数不是myPromise实例
		if(value instanceof myPromise){
			//如参数是一个promise实例
			value.parent = this;

			if(value.state===1){
				//实例状态为成功
				myPromise.resolve.call(this,value.successArg);
			}else if(value.state===-1){
				//实例状态为失败
				myPromise.reject.call(this,value.failArg);
			}
		}else{
			if(!this.success.length){
				for(let fn of this.fork){
					myPromise.resolve.call(fn,value);
				}
			}else{
				this.sucArg = value;
				//将参数的链式传递
				let cur = 0;

				for(let fn of this.success){
					if(this.fork.length&&fn===this.fork[cur].forkSuc){
						let error,preArg,bool;
						try{
							preArg = fn(value);
							if(preArg instanceof myPromise){
								let index = cur;
								bool = true;
								preArg.success.push(()=>{
									myPromise.resolve.call(this.fork[index],preArg.sucArg);
								});
								preArg.fail.push(()=>{
									myPromise.reject.call(this.fork[index],preArg.failArg);
								})
							}
						}catch(err){
							error = err||null; 
						}
						if(!bool){
							error? myPromise.reject.call(this.fork[cur],error): myPromise.resolve.call(this.fork[cur],preArg);
						}
						
						cur++;
					}else{
						fn(value);
					}
					

				}

			}
			
			//当前promise变为fulfilled
			this.state = 1;

			//如存在parent,则parent.resolve(value)
			if(this.parent) myPromise.resolve.call(this.parent,value);
		}
	}
	static reject(value){
		
		if(this.state!==0) return;
		//参数是myPromise实例
		if(value instanceof myPromise){

			value.parent = this;
			myPromise.reject.call(this);

		}else{
			if(!this.fail.length){
				for(let fn of this.fork){
					myPromise.reject.call(fn,value)
				}
			}else{
				//执行错误处理
				this.failArg = value;
				let cur = 0;
				//遍历执行剩下的fail
				for(let fn of this.fail){
	
					if(this.fork.length&&fn===this.fork[cur].forkRej){
						let error,preArg,bool;
						try{
							preArg = fn(value);
							if(preArg instanceof myPromise){
								let index = cur;
								bool = true;
								preArg.success.push(()=>{
									myPromise.resolve.call(this.fork[index],preArg.sucArg);
								});
								preArg.fail.push(()=>{
									myPromise.reject.call(this.fork[index],preArg.failArg);
								})
							}
						}catch(err){
							error = err||null; 
						}
						if(!bool){
							error? myPromise.reject.call(this.fork[cur],error): myPromise.resolve.call(this.fork[cur],preArg);
							
						}
						cur++;
						
					}else{
						fn(value);
					}	
				}
			}
			
			this.state = -1;

			if(this.parent) myPromise.reject.call(this.parent,value);
		}
		
	}
	then(onFulfilled,onRejected){
		/*参数格式限制*/
		let fork = new myPromise(()=>{});

		if(typeof onFulfilled ==='function'){
			this.success.push(onFulfilled);
			fork.forkSuc = onFulfilled;
		}

		if(typeof onRejected ==='function'){
			this.fail.push(onRejected);
			fork.forkRej = onRejected;
		} 
		
		this.fork.push(fork);

		return fork;
	}
	catch(onRejected){		
		//参数必须函数
		let fork = new myPromise(()=>{});

		if(typeof onRejected !=='function') return this;
		
		//设置错误节点位置，和错误处理函数
		this.fail.push(onRejected);
		fork.forkRej = onRejected;	
		this.fork.push(fork);

		return fork;
	}
}

export default{
	promise: myPromise,
};


