<template>
	<div class="signup">
		<validator name="validator2" :group="['userName','password','email']" @valid="onValid()" @invalid="inValid()">
			<form class="form-horizontal" role="form">
			   <div class="form-group">
			      	<label for="firstname" class="control-label">用户名</label>
			      
			         <input type="text" class="form-control" id="userName" v-model="Data.userName" group="userName"
			            placeholder="请输入用户名" detect-change="off" v-validate:userName="{
			            	required:{rule: true,message:'请输入用户名'},
			            	name: {rule: true,message:'字母开头,只包含字母，字符，数字，下划线'},
			            	exist: {rule: true,message:'该用户名已存在',initial:'off'},
			            	}"><span  v-if="$validator2.username.invalid" class="icon-remove-sign i"></span>
			        	
			        	<div v-if="$validator2.username.invalid" class="error-wraper">
			        		<validator-errors partial="myErrorTemplate" group="username" :validation="$validator2"></validator-errors>	
			        	</div>
						      
			   </div>
			   <div class="form-group">
			      <label for="lastname" class="control-label">密码</label>
			      
			         <input type="password" class="form-control"  id="password" v-model="Data.password" group="password" v-validate:password="{
			         	required:{rule: true, message: '请输入密码'},
			         	pass: { rule: true, message: '只包含字母，字符，数字，下划线'},
			         	maxlength: { rule: 16, message: '长度在6-16之间'},
			         	minlength: { rule: 6, message: '长度在6-16之间'},
			         	}"
			            placeholder="请输入密码"><span  v-if="$validator2.password.invalid" class="icon-remove-sign i"></span>
			            <div v-if="$validator2.password.invalid" class="error-wraper">
			            	<validator-errors partial="myErrorTemplate" group="password" :validation="$validator2"></validator-errors>	
			            </div>
			            
			   
			   </div>
			   <div class="form-group">
			      <label for="lastname" class="control-label">确认密码</label>
			      
			         <input type="password" class="form-control" id="confirm"  group="confirm" v-validate:confirm="{
			         	required:{rule: true, message: '请再次输入密码'},
						 confirm:{rule: true, message: '两次输入的密码不一致'},
			         	}"
			            placeholder="请再次输入密码"><span  v-if="$validator2.confirm.invalid" class="icon-remove-sign i"></span>
			            <div v-if="$validator2.confirm.invalid" class="error-wraper">
			            	<validator-errors partial="myErrorTemplate" group="confirm" :validation="$validator2"></validator-errors>	
			            </div>
			            <!-- <validator-errors group="confirm" :validation="$validator2"></validator-errors> -->
			      
			   </div>
			   <div class="form-group">
			      <label for="lastname" class="control-label">邮箱</label>
			      
			         <input type="text" class="form-control" id="email" v-model="Data.email"  group="email" v-validate:email="{
			         	required:{ rule: true, message: '请输入您的邮箱'},
			         	email: { rule: true , message: '请输入正确的邮箱格式'},
			         	}"
			            placeholder="请输入邮箱"><span  v-if="$validator2.email.invalid" class="icon-remove-sign i"></span>
			            
			            <div v-if="$validator2.email.invalid" class="error-wraper">
			            	<validator-errors partial="myErrorTemplate" group="email" :validation="$validator2"></validator-errors>	
			            </div>
			      
			   </div>
			</form>
		
    			
  			
		</validator>
		
	</div>
</template>
<script>
import Vue from 'vue'
import validator from 'vue-validator'

Vue.use(validator);

Vue.partial('myErrorTemplate','<span>{{message}}</span>');


	export default {
		data(){
			return {
				checking: false,
			}
		},
		validators:{
			name(val){
				return /^[a-zA-Z]\w*$/.test(val);
			},
			pass(val){
				return /^\w*$/.test(val);	
			},
			email(val){
				return /^(\w)+(\.\w+)*@(\w)+((\.\w{2,3}){1,3})$/.test(val);
			},
			confirm(val){
				
				return val===this.vm.Data.password;
			},
			exist(val){
				return this.vm.$http.post('/exist',{userName:val}).then((res)=>{
					
					return Promise.resolve()
				}).catch((err)=>{

					return Promise.reject()
					
				})
			}
		},
		methods: {
			onValid(){
				this.$dispatch('signupOnValid');
			},
			inValid(){
				this.$dispatch('signupInValid');
			}
		},
		ready(){
			this.Data = {};
		},
		props:['Data'],
		
	}
</script>
<style lang="scss" scoped>
	$width-small:  400px;
	$width-medium: 760px;
	$width-large:  1200px;
	$inputHeight: 40px;
	@mixin responsive($width) {
	    @if $width == wide-screens {
	        @media only screen and (max-width: $width-large) { @content; }
	    }
	    @else if $width == medium-screens {
	        @media only screen and (max-width: $width-medium) { @content; }
	    }
	    @else if $width == small-screens {
	        @media only screen and (max-width: $width-small) { @content; }
	    }
	}
	.signup {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		display: block;
		max-width:500px;
		min-width: 320px;
		width: 100%;
		
		font-size: 1rem;
		line-height: 1;
		form {
			overflow: visible;
			

		}
		.fl {
			float: left;
		}
		.fr {
			float: right;
		}
		.form-group {
			width: 100%;
			margin: 20px 0;
			padding: 0 30px;
			position: relative;
			
		}
		
		input {
			float: left;
			height: $inputHeight;
			
		}
		label {
			text-align: left;
			display: block;
			color: white;
			font-weight: normal;
			font-size: 16px;
			margin-bottom: 10px;
			padding: 0;
		}
		.i {
			display: inline;
			float: left;
			margin-left: -20px;
			margin-top: ($inputHeight - 13)/2;;
			color: red;
		}

		.error-wraper {
			position: absolute;
			buttom: 0;
			right: -170px;
			width: 180px;
			text-align: left;
			background-color: #3594CB;
			line-height: 1.5;
			padding: 10px;
			border-radius: 10px;
			&::before {
				content: '';
			    display: block;
			    position: absolute;
			    left: -10px;
				top: 10px;
			    width: 0;
			    height: 0;
			    border-right: 10px solid #3594CB;
			    border-top: 5px solid transparent;
			    border-bottom: 5px solid transparent;
			}
			@include responsive(medium-screens) {
				display: none;
			}

		}
	}
</style>


