<template>
  <Logo></Logo>  
  <div class="intro">
    <span v-if="textState">来一场没有硝烟的战争,<br>体验战斗后的酣畅 !<br>出发吧 ！</span>
    <router-view v-if="!textState" :Data.sync="Data" :is="current"></router-view>
    
  </div>
  <div class="btn-set">
    <a class="btn btn-default btn-login" :disabled="buttonState.signin" @click="dosignin()">登录</a>
    <a class="btn btn-primary btn-signup" :disabled="buttonState.signup" @click="dosignup()">注册</a>
  </div>
  <spinner v-ref:spinner :size="md" :fixed="true" text="服务器正在响应，请稍候......."></spinner>
  <alert
    :show.sync="error"
    :duration="3000"
    type="danger"
    width="100%"
    placement="top"
    dismissable="false"
    >
      <strong>错误！</strong>
      <p>请检查您的用户名或密码^_^没有帐号的注册一下喽^_^</p>
  </alert>
  <modal :show.sync="signupError" effect="fade" width="400">
    <div slot="modal-header" class="modal-header">
      <h4 class="modal-title">
        <code>服务器或数据库错误</code>
      </h4>
    </div>
    <div slot="modal-body" class="modal-body">
      请联系作者: 387857274 或 gloria_n@yeah.net
    </div>
    <div slot="modal-footer" class="modal-footer">
      <button type="button" class="btn btn-success" @click='signupError = false'>关闭</button>
    </div>
  </modal>
</template>

<script>
import lineCenter from '../lib/js/lineCenter'
import Signin from './unsigned/Signin.vue'
import Signup from './unsigned/Signup.vue'
import Logo from './header'
import myPromise from '../lib/js/myPromise.js'
import VueStrap from 'vue-strap'
import VueRouter from 'vue-router'
import Vue from 'vue'

Vue.use(VueRouter);



export default {
  data(){
    return {
      current:null,
      textState: true,
      buttonState:{
        signin: false,
        signup: false,
      },
      Data: {},
      error: false,
      spinnerText: '',
      signupError: false,
    }
  },
  computed:{
    
  },
  methods:{
    dosignin(){
      if(this.buttonState.signin) return;
      if(this.current=='Signin'){
        this.$refs.spinner.show();
        setTimeout(this.signin,1000)
      }else{
        //进入登陆界面
        this.$route.router.replace('signin');
        this.textState = false;
        this.buttonState = {signin: true,signup: false};
        this.current = 'Signin';  
      }
      
      //this.$dispatch('signed');  
      
    },
    signin(){
      let {userName,password} = this.Data;
      //发送登录请求
      this.$http.post('/signin',{userName:userName,password:password}).then((resp)=>{
        let data = resp.text()=='-1'||resp.text()==''? false:resp.json();
        if(data){
          //移除事件
          window.onkeyup = null;
          this.$dispatch('signed',data);

        }else{
          //错误
          this.error = true;
          this.$refs.spinner.hide();
        }
      }).catch((err)=>{
        console.log(err);
      });
    },
    dosignup(){ 
      if(this.buttonState.signup) return;
      if(this.current=='Signup'){
         this.$refs.spinner.show();
         setTimeout(this.signup,1000);
      }else{
        //进入注册界面
        this.$route.router.replace('signup');
        this.textState = false;
        this.buttonState = {signin: false,signup: true};
        this.current = 'Signup';  
      }
    },
    signup(){
      let {userName,password,email} = this.Data;
      //发送注册请求
      this.$http.post('/signup',{userName:userName,password:password,email:email}).then((resp)=>{
        let data = resp.text()=='false'? false:resp.json();

        if(data){
          //正确收到数据
          //移除事件
          window.onkeyup = null;
          //将数据传递给父组件
          setTimeout(()=>{this.$dispatch('signed',data);},2000);
          
        }else{
          //出错
          this.signupError = true;
        }


      }).catch((err)=>{
        console.log(err)
      })
    },
    Keyup(e){
      let curView = this.current;
      let kCode = e.keyCode;

      if(!(kCode===13)) return;

      if(curView=='Signup'){
        this.dosignup();
      }else if(curView=='Signin'){
        this.dosignin();
      }
    }
  },
  ready(){
    lineCenter.do('.intro');
    lineCenter.do('.btn-set');
    //添加enter事件
    window.onkeyup = this.Keyup;
  },
  components: {
    Signin,Signup,
    spinner: VueStrap.spinner,
    alert: VueStrap.alert,
    modal: VueStrap.modal,
    Logo,
  },
  events:{
    'signinOnValid': function(){
      this.buttonState.signin = false;
    },
    'signupOnValid': function(){
      this.buttonState.signup = false;
    },
    'signupInValid': function(){
      this.buttonState.signup = true;
    },
    'signinInValid': function(){
      this.buttonState.signin = true;
    }
  }
}
</script>

<style lang="scss" scoped>
  @mixin center{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  .intro {
    position: relative;
    font-size: 2.5rem;
    text-align: center;
    height: 70%;
    color: white;
    
    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0,0,0,0.5);

    }
    span {
      display: inline-block;
      position: absolute;
      min-width: 320px;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      line-height: 1.5em;
      color: white;
    }
  }
  .btn-set {
    min-width: 320px;
    text-align: center;
    height: 15%;
    background-color: white;
    .btn-login,.btn-signup {
      width: 100px;
      font-size: 2rem;
    }
  }
</style>



