<template>
	<div class="signin">
		<validator name="validator1" 
					@valid="onValid()"
					@invalid="inValid()"	>
			<form class="form-horizontal" role="form">
			   <div class="form-group">
			      <label for="firstname" class="control-label">用户名</label>
			      
		         <input type="text" class="form-control" id="userName" v-validate:userName="{
		         	required: {rule:true,message:'请输入您的用户名'},
		         	}" v-model="Data.userName"
		            placeholder="请输入用户名"><span v-if="$validator1.username.invalid" class="icon-remove-sign i"></span>
			      
			   </div>
			   
			   <div class="form-group">
			      <label for="lastname" class="control-label">密码</label>
			      
		         <input type="password" class="form-control" id="password" v-validate:password="{
		         	required:{rule:true,message:'请输入您的密码'},
		         	minlength:{rule:6,message:'密码长度在6-16个字符'},
		         	maxlength:{rule:16,message:'密码长度在6-16个字符'},
		         	}" v-model="Data.password"
		            placeholder="请输入密码"><span  v-if="$validator1.password.invalid" class="icon-remove-sign i"></span>
			      
			   </div>
			   
			</form>	
		</validator>
		
	</div>
</template>
<script>
	import validator1 from 'vue-validator'
	import Vue from 'vue'
	
	
	Vue.use(validator1);
	export default {
		data(){
			return {
				userName: null,
				password: null,
			}
		},
		methods: {
			onValid(){
				this.$dispatch('signinOnValid');
			},
			inValid(){
				this.$dispatch('signinInValid');
			},	
		},
		components:{
		},
		validators:{
		},
		ready(){
			this.Data = {};
		},
		props:['Data'],
	}
</script>
<style lang="scss" scoped>
	$width-small:  400px;
	$width-medium: 760px;
	$width-large:  1200px;
	$inputHeight: 40px;
	@mixin responsive($width) {
	    @if $width == wide-screens {
	        @media only screen and (max-width: $width-large) { @content; }
	    }
	    @else if $width == medium-screens {
	        @media only screen and (max-width: $width-medium) { @content; }
	    }
	    @else if $width == small-screens {
	        @media only screen and (max-width: $width-small) { @content; }
	    }
	}
	.signin {
		display:block;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		width: 100%;
		max-width: 400px;
		min-width: 320px;
		line-height: 1;
		vertical-align: middle;
		font-size: 1rem;
		
		label {
			text-align: left;
			display: block;
			color: white;
			font-weight: normal;
			font-size: 16px;
			margin-bottom: 10px;
			padding: 0;
			@include responsive(small-screens) {
				display: block;
				
			}
		}

		.form-group {
			width: 100%;
			margin: 10px 0;
			padding: 0 30px;
		}
		input {
			float: left;
			height: $inputHeight;
		}
		.i {
			display: inline;
			float: left;
			margin-left: -20px;
			margin-top: ($inputHeight - 13)/2;
			color: red;
		}
		
		
	}
</style>


