import levelMap from './levelMap.js'
import toSecond from './toSecond.js'
export default {
	second(user,str){
		let level = user.current.mission[6];
		let min = user.gameInfo['level'+level][str];
		return toSecond.do(min);
	},
	arg(second,num){
		let k,a;
		if(second>0&&second<=600){
			k=0.9;
		}else if(second>600&&second<=1200){
			k=0.8;
		}else if(second>1200&&second<=1800){
			k=0.7;
		}else if(second>1800&&second<=2400){
			k=0.6;
		}else if(second>2400&&second<=3000){
			k=0.5;
		}else if(second>3000&&second<=3600){
			k=0.4;
		}else{
			k=0.3;
		}
		if(num=='1'){
			a=0;
		}else if(num=='2'){
			a=600;
		}else if(num=='3'){
			a=1200;
		}else if(num=='4'){
			a=1800;
		}
		return {
			k:k,
			a:a
		}
	}
}


