import timeFormat from './timeFormat.js'
import levelMap from './levelMap.js'
function observeAndFormat(obj){
	let result = {};

	Object.assign(result,obj);
	//转换gameInfo中的时间
	for(let i=1;i<5;i++){
		let min = result.gameInfo['level'+i].min;
		let avg = result.gameInfo['level'+i].avg;

		result.gameInfo['level'+i].min = timeFormat.do(min);
		result.gameInfo['level'+i].avg = timeFormat.do(avg);
	}
	//转换current 中的时间
	let time = result.current.time;
	result.current.time = timeFormat.do(time)

	//转换current 中的mission
	let level = parseInt(result.current.mission.slice(0,1));
	let subLevel = parseInt(result.current.mission.slice(2));


	level = levelMap.map[level];
	result.current.mission = level+subLevel+'层';
	result.level = level;


	return result;
}

export default {
	do(obj){
		return observeAndFormat(obj);
	}
};


