export default{
	do(ele){
		ele = document.querySelector(ele);
		let elS = window.getComputedStyle(ele,null);
		let h = parseFloat(elS.getPropertyValue('height'));
		let mT = parseFloat(elS.getPropertyValue('margin-top'));
		let mB = parseFloat(elS.getPropertyValue('margin-bottom'));
		let pB = parseFloat(elS.getPropertyValue('padding-bottom'));
		let pT = parseFloat(elS.getPropertyValue('padding-top'));
		let result = h-mT-mB-pB-pT;
		ele.style.lineHeight = result+'px';
	}
}


