export default {
	do(str){
		let hour,minute,second;
		for(let i=0;i<str.length;i++){
			if(str[i+1]=='小'){
				hour = str.slice(0,i+1);
			}else if(str[i+1]=='分'){
				let start = i-1;
				if( i-1<0 || str[i-1]=='时'){
					start = i;
				}
				minute = str.slice(start,i+1);

			}else if(str[i+1]=='秒'){
				let start = i-1;
				if( i-1<0 || str[i-1]=='分'){
					start = i;
				}
				second = str.slice(start,i+1);
			}
		}
		hour = hour==void 0? 0:parseInt(hour);
		minute = minute==void 0? 0:parseInt(minute);
		second = second==void 0? 0:parseInt(second);
		return hour*3600+minute*60+second;
	}
}


